import {
  Component,
  OnInit,
} from '@angular/core';

import ApiPaymentFindModel from '../../../../_api/payment/find/output';
import ApiPaymentFindService from '../../../../_api/payment/find/service'

@Component({
  selector: 'my-page-payment-list',
  template: require('./template.html'),
})
export default class View implements OnInit {
  private data: ApiPaymentFindModel[];

  public constructor(
    private apiPaymentFind: ApiPaymentFindService,
  ) {}

  public ngOnInit(): void {
    this.apiPaymentFind.exec({}).subscribe(result => {
      this.data = result;
    }, err => {});
  }
}



