import {
  Component,
  OnInit,
} from '@angular/core';
import { Router } from '@angular/router';

import ApiBankFindModel from '../../../../_api/bank/find/output';
import ApiBankFindService from '../../../../_api/bank/find/service';
import ApiPaymentCreateService from '../../../../_api/payment/create/service';

import SessionService  from '../../../../_services/session';

@Component({
  selector: 'my-page-payment-add',
  template: require('./template.html'),
})
export default class View implements OnInit {
  private dataBankList: ApiBankFindModel[];
  private formAmount: number = 50;
  private formBank: string = null;

  public constructor(
    private apiBankFind: ApiBankFindService,
    private apiPaymentCreate: ApiPaymentCreateService,
    private router: Router,
    private session: SessionService,
  ) {}

  public ngOnInit(): void {
    this.apiBankFind.exec({
      company: this.session.user.company,
    }).subscribe(result => {
      this.dataBankList = result;
    }, err => {});
  }

  private submit(): void {
    this.apiPaymentCreate.exec({
      bank: this.formBank,
      amount: this.formAmount,
    }).subscribe(result => {
      this.router.navigate(['/app/page/payment']);
    }, err => {});
  }
}



