import {
  Component,
  OnInit,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import ApiPaymentFindModel from '../../../../_api/payment/find/output';
import ApiPaymentFindService from '../../../../_api/payment/find/service';

@Component({
  selector: 'my-page-payment-view',
  template: require('./template.html'),
})
export default class View implements OnInit {
  private data: ApiPaymentFindModel = null;
  private dataBankAddress: string = null;
  private dataBeneficiaryAddress: string = null;
  private dataIntermediaryBank: string = null;
  private paramsId: string = null;

  public constructor(
    private route: ActivatedRoute,
    private apiPaymentFind: ApiPaymentFindService,
  ) {}

  public ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.paramsId = params.id;

      this.apiPaymentFind.exec({
        id: this.paramsId,
      }).subscribe(result => {
        this.data = result[0];
        this.dataBankAddress = this.data.paymentTransfer.chargeInfo.wireDetails.bankAddress.replace(/<br \/> ?/g, '\n');
        this.dataBeneficiaryAddress = this.data.paymentTransfer.chargeInfo.wireDetails.beneficiaryAddress.replace(/<br \/> ?/g, '\n');
        this.dataIntermediaryBank = this.data.paymentTransfer.chargeInfo.wireDetails.intermediaryBank.replace(/<br \/> ?/g, '\n');
      }, err => {});
    });
  }
}



