import { Routes, RouterModule } from '@angular/router';

import { PagesComponent } from './pages.component';

import PageBankAddComponent from './bank/add/main/script';
import PageBankListComponent from './bank/list/main/script';
import PageBankViewComponent from './bank/view/main/script';

import PageCompanyAddComponent from './company/add/company.add.component';
import PageCompanyListComponent from './company/list/company.list.component';
import PageCompanyViewComponent from './company/view/company.view.component';

import PagePaymentAddComponent from './payment/add/main/script';
import PagePaymentListComponent from './payment/list/main/script';
import PagePaymentViewComponent from './payment/view/main/script';

import PageProviderAddComponent from './provider/add/provider.add.component';
import PageProviderEditComponent from './provider/edit/script';
import PageProviderListComponent from './provider/list/provider.list.component';
import PageProviderViewComponent from './provider/view/script';

import { PageUsersComponent } from './users/users.component';
import PageUsersAddComponent from './users/add/users.add.component';
import PageUsersViewComponent from './users/view/users.view.component';

import PageWalletListComponent from './wallet/list/wallet.list.component';

import { AuthGuard } from '../_guards/auth.guard';

export const PagesRoutes: Routes = [
  {
    path: '',
    component: PagesComponent,
    children: [
      { path: '', redirectTo: '/app/dashboard', pathMatch: 'full' },
      { path: 'bank', component: PageBankListComponent, canActivate: [AuthGuard] },
      { path: 'bank/add', component: PageBankAddComponent, canActivate: [AuthGuard] },
      { path: 'bank/view/:id', component: PageBankViewComponent, canActivate: [AuthGuard] },
      { path: 'company', component: PageCompanyListComponent, canActivate: [AuthGuard] },
      { path: 'company/add', component: PageCompanyAddComponent, canActivate: [AuthGuard] },
      { path: 'company/view/:id', component: PageCompanyViewComponent, canActivate: [AuthGuard] },
      { path: 'payment', component: PagePaymentListComponent, canActivate: [AuthGuard] },
      { path: 'payment/add', component: PagePaymentAddComponent, canActivate: [AuthGuard] },
      { path: 'payment/view/:id', component: PagePaymentViewComponent, canActivate: [AuthGuard] },
      { path: 'provider', component: PageProviderListComponent, canActivate: [AuthGuard] },
      { path: 'provider/add', component: PageProviderAddComponent, canActivate: [AuthGuard] },
      { path: 'provider/edit/:id', component: PageProviderEditComponent, canActivate: [AuthGuard] },
      { path: 'provider/view/:id', component: PageProviderViewComponent, canActivate: [AuthGuard] },
      { path: 'users', component: PageUsersComponent, canActivate: [AuthGuard] },
      { path: 'users/add', component: PageUsersAddComponent, canActivate: [AuthGuard] },
      { path: 'users/view/:id', component: PageUsersViewComponent, canActivate: [AuthGuard] },
      { path: 'wallet', component: PageWalletListComponent, canActivate: [AuthGuard] },
    ]
  }
];

export const PagesRoutingModule = RouterModule.forChild(PagesRoutes);



