import { Component, OnInit } from '@angular/core';

import UserFindOutput from '../../_api/user/find/output';
import UserFindService from '../../_api/user/find/service'

@Component({
  selector: 'my-page-users',
  template: require('./users.component.html'),
})
export class PageUsersComponent implements OnInit {
  userList: UserFindOutput[];

  constructor(
    private userFindService: UserFindService,
  ) {}

  ngOnInit(): void {
    this.userFindService.exec({}).subscribe(result => {
      this.userList = result;
    }, err => {});
  }
}



