import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import ApiCompanyFindModel from '../../../_api/company/find/output';
import ApiCompanyFindService from '../../../_api/company/find/service';
import ApiProviderCreateService from '../../../_api/provider/create/service';

import SessionService  from '../../../_services/session';

@Component({
  selector: 'my-page-provider-add',
  template: require('./provider.add.component.html'),
})
export default class View implements OnInit {
  private company: ApiCompanyFindModel = null;
  private formAccountNumber: string = null;
  private formBeneficiaryType: string = null;
  private formClabe: string = null;
  private formFirstName: string = '';
  private formLastName: string = '';
  private formBeneficiaryName: string = '';
  private uiError: boolean = false;
  private uiWaiting: boolean = false;

  public constructor(
    private apiCompanyFind: ApiCompanyFindService,
    private apiProviderCreate: ApiProviderCreateService,
    private route: ActivatedRoute,
    private router: Router,
    private session: SessionService,
  ) {}

  public ngOnInit(): void {
    this.apiCompanyFind.exec({
      id: this.session.user.company,
    }).subscribe(company => {
      this.company = company[0];
    }, err => {});

    this.route.queryParams.subscribe(params => {
      this.formBeneficiaryType = params.type;
    });
  }

  private formBeneficiaryTypeChanged(newValue): void {
    this.router.navigate([], {
      queryParams: {
        type: newValue,
      },
      replaceUrl: true,
    });
  }

  private submit(): void {
    switch(this.formBeneficiaryType) {
      case 'CORPORATE':
        var name = this.formBeneficiaryName;
        break;
      case 'INDIVIDUAL':
        var name = `${this.formFirstName} ${this.formLastName}`;
        break;
    }

    this.apiProviderCreate.exec({
      accountNumber: this.formAccountNumber,
      beneficiaryType: this.formBeneficiaryType,
      clabe: this.formClabe,
      company: this.company.id,
      country: 'MX',
      firstName: this.formFirstName,
      lastName: this.formLastName,
      beneficiaryName: this.formBeneficiaryName,
      name: name,
    }).subscribe(result => {
      const ts2 = timeout - (Date.now() - ts);
      setTimeout(() => {
        this.router.navigate(['/app/page/provider']);
      }, ts2 > 0 ? ts2 : 0);
    }, err => {
      const ts2 = timeout - (Date.now() - ts);
      setTimeout(() => {
        this.uiError = true;
        this.uiWaiting = false;
      }, ts2 > 0 ? ts2 : 0);
    });

    const timeout = 1000;
    const ts = Date.now();
    this.uiWaiting = true;
  }
}



