import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import ApiCompanyFindModel from '../../../_api/company/find/output';
import ApiCompanyFindService from '../../../_api/company/find/service';
import ApiUserCreateService from '../../../_api/user/create/service';

@Component({
  selector: 'my-page-users-add',
  template: require('./users.add.component.html'),
})
export default class PageUsersAddComponent implements OnInit {
  dataCompanies: ApiCompanyFindModel[];
  formCompany: string;
  formEmail: string;
  formPassword: string;

  constructor(
    private apiCompanyFind: ApiCompanyFindService,
    private apiUserCreate: ApiUserCreateService,
    private router: Router,
  ) {}

  ngOnInit(): void {
    this.apiCompanyFind.exec({}).subscribe(result => {
      this.dataCompanies = result;
    }, err => {});
  }

  submit(): void {
    this.apiUserCreate.exec({
      company: this.formCompany,
      email: this.formEmail,
      password: this.formPassword,
    }).subscribe(result => {
      this.router.navigate(['/app/page/users']);
    }, err => {});
  }
}



