import { Component } from '@angular/core';
import { Router } from '@angular/router';

import ApiCompanyCreateService from '../../../_api/company/create/service';

@Component({
  selector: 'my-page-company-add',
  template: require('./company.add.component.html'),
})
export default class View {
  name: string;

  constructor(
    private apiCompanyCreate: ApiCompanyCreateService,
    private router: Router,
  ) {}

  submit(): void {
    this.apiCompanyCreate.exec({
      name: this.name,
    }).subscribe(result => {
      this.router.navigate(['/app/page/company']);
    }, err => {});
  }
}



