import {
  Component,
  OnInit,
} from '@angular/core';
import { Router } from '@angular/router';

import ApiBankCreateService from '../../../../_api/bank/create/service';
import ApiCompanyFindModel from '../../../../_api/company/find/output';
import ApiCompanyFindService from '../../../../_api/company/find/service';

import SessionService  from '../../../../_services/session';

@Component({
  selector: 'my-page-bank-add',
  template: require('./template.html'),
})
export default class View implements OnInit {
  private dataCompany: ApiCompanyFindModel = null;
  private formName: string = '';
  private formBeneficiaryType: string = 'INDIVIDUAL';
  private formAccountType: string = 'CHECKING';
  private formAccountNumber: string = '';
  private formRoutingNumber: string = '';
  private formFirstNameOnAccount: string = '';
  private formLastNameOnAccount: string = '';
  private formBeneficiaryCompanyName: string = '';
  private formBeneficiaryEinTin: string = '';
  private formBeneficiaryAddress: string = '';
  private formBeneficiaryAddress2: string = '';
  private formBeneficiaryCity: string = '';
  private formBeneficiaryPostal: string = '';
  private formBeneficiaryState: string = '';
  private formBeneficiaryPhoneNumber: string = '';
  private formBeneficiaryLandlineNumber: string = '';
  private formBeneficiaryEmailAddress: string = '';
  private formDateOfBeneficiary: Date = new Date();

  public constructor(
    private apiCompanyFind: ApiCompanyFindService,
    private apiBankCreate: ApiBankCreateService,
    private router: Router,
    private session: SessionService,
  ) {}

  public ngOnInit(): void {
    this.apiCompanyFind.exec({
      id: this.session.user.company,
    }).subscribe(company => {
      this.dataCompany = company[0];
    }, err => {});
  }

  private submit(): void {
    this.apiBankCreate.exec({
      name: this.formName,
      company: this.dataCompany.id,
      beneficiaryType: this.formBeneficiaryType,
      accountType: this.formAccountType,
      accountNumber: this.formAccountNumber,
      routingNumber: this.formRoutingNumber,
      firstNameOnAccount: this.formFirstNameOnAccount,
      lastNameOnAccount: this.formLastNameOnAccount,
      beneficiaryCompanyName: this.formBeneficiaryCompanyName,
      beneficiaryEinTin: this.formBeneficiaryEinTin,
      beneficiaryAddress: this.formBeneficiaryAddress,
      beneficiaryAddress2: this.formBeneficiaryAddress2,
      beneficiaryCity: this.formBeneficiaryCity,
      beneficiaryPostal: this.formBeneficiaryPostal,
      beneficiaryState: this.formBeneficiaryState,
      beneficiaryPhoneNumber: this.formBeneficiaryPhoneNumber,
      beneficiaryLandlineNumber: this.formBeneficiaryLandlineNumber,
      beneficiaryEmailAddress: this.formBeneficiaryEmailAddress,
      beneficiaryDobDay: this.formDateOfBeneficiary.getDate().toString(),
      beneficiaryDobMonth: (this.formDateOfBeneficiary.getMonth() + 1).toString(),
      beneficiaryDobYear: this.formDateOfBeneficiary.getFullYear().toString(),
    }).subscribe(result => {
      this.router.navigate(['/app/page/bank']);
    }, err => {});
  }
}



