import {
  Component,
  OnInit,
} from '@angular/core';

import ApiBankFindModel from '../../../../_api/bank/find/output';
import ApiBankFindService from '../../../../_api/bank/find/service'

@Component({
  selector: 'my-page-bank-list',
  template: require('./template.html'),
})
export default class View implements OnInit {
  private data: ApiBankFindModel[];

  public constructor(
    private apiBankFind: ApiBankFindService,
  ) {}

  public ngOnInit(): void {
    this.apiBankFind.exec({}).subscribe(result => {
      this.data = result;
    }, err => {});
  }
}



