import { Component, OnInit } from '@angular/core';

import ApiCompanyFindModel from '../../../_api/company/find/output';
import ApiCompanyFindService from '../../../_api/company/find/service'

@Component({
  selector: 'my-page-company',
  template: require('./company.list.component.html'),
})
export default class View implements OnInit {
  companyList: ApiCompanyFindModel[];

  constructor(
    private apiCompanyFind: ApiCompanyFindService,
  ) {}

  ngOnInit(): void {
    this.apiCompanyFind.exec({}).subscribe(result => {
      this.companyList = result;
    }, err => {});
  }
}



