import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import ApiCompanyFindService from '../../../_api/company/find/service';

@Component({
  selector: 'my-page-company-view',
  template: require('./company.view.component.html'),
})
export default class PageCompanyViewComponent implements OnInit {
  companyId: string;
  name: string;

  constructor(
    private apiCompanyFind: ApiCompanyFindService,
    private route: ActivatedRoute,
    private router: Router,
  ) {}

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.companyId = params.id;
      this.apiCompanyFind.exec({
        id: params.id,
      }).subscribe(companies => {
        const company = companies[0]
        if (company !== undefined) {
          this.name = company.name;
        }
      }, err => {});
    });
  }
}



