import {
  Component,
  OnInit,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import ApiBankFindModel from '../../../../_api/bank/find/output';
import ApiBankFindService from '../../../../_api/bank/find/service';
import ApiSwPaymentMethodsService from '../../../../_api/sw/paymentMethods/service';

@Component({
  selector: 'my-page-bank-view',
  template: require('./template.html'),
})
export default class View implements OnInit {
  private data: ApiBankFindModel = null;
  private paramsId: string = null;

  public constructor(
    private route: ActivatedRoute,
    private apiBankFind: ApiBankFindService,
    private apiSwPaymentMethods: ApiSwPaymentMethodsService,
  ) {}

  public ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.paramsId = params.id;
      this.apiBankFind.exec({
        id: this.paramsId,
      }).subscribe(data => {
        this.data = data[0];
      }, err => {});
    });
  }

  private link(): void {
    this.apiSwPaymentMethods.exec({
      bank: this.paramsId,
    }).subscribe(result => {
      console.log(result);
    }, err => {});
  }
}



