import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';

import ApiCompanyFindModel from '../../../_api/company/find/output';
import ApiCompanyFindService from '../../../_api/company/find/service';

import WalletListTransferDialogComponent from './components/transfer-dialog';

@Component({
  selector: 'my-page-wallet-list',
  template: require('./wallet.list.component.html'),
})
export default class View implements OnInit {
  dataCompanies: ApiCompanyFindModel[];
  formAmount: number = 10.00;
  formDestination: string;

  constructor(
    private apiCompanyFind: ApiCompanyFindService,
    private dialog: MatDialog,
  ) {}

  ngOnInit(): void {
    this.apiCompanyFind.exec({}).subscribe(result => {
      this.dataCompanies = result;
    }, err => {});
  }

  submit() {
    const dialogRef = this.dialog.open(WalletListTransferDialogComponent, {
      data: {
        amount: this.formAmount,
        company: this.formDestination,
        destination: this.dataCompanies.find(i => i.id === this.formDestination).wallet.id,
      },
    });
    dialogRef.afterClosed().subscribe(result => {});
  }
}



