import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

import ApiProviderDestroyService from '../../../../_api/provider/destroy/service'
import ApiProviderFindOneModel from '../../../../_api/provider/findOne/output';
import ApiProviderFindOneService from '../../../../_api/provider/findOne/service';

@Component({
  selector: 'my-page-provider-view-remove',
  template: require('./remove.html')
})
export default class View implements OnInit {
  private data: ApiProviderFindOneModel = null;

  public constructor(
    private apiProviderDestroy: ApiProviderDestroyService,
    private apiProviderFindOne: ApiProviderFindOneService,
    private dialogRef: MatDialogRef<View>,
    @Inject(MAT_DIALOG_DATA) private input: any,
  ) {}

  public ngOnInit() {
    this.apiProviderFindOne.exec({
      id: this.input,
    }).subscribe(result => {
      this.data = result;
    }, err => {});
  }

  confirm() {
    this.apiProviderDestroy.exec({
      id: this.data.id,
    }).subscribe(result => {
      this.dialogRef.close(true);
    }, err => {});
  }
}



