import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import ApiProviderFindOneModel from '../../../_api/provider/findOne/output';
import ApiProviderFindOneService from '../../../_api/provider/findOne/service';
import ApiProviderUpdateService from '../../../_api/provider/update/service';

@Component({
  selector: 'my-page-provider-edit',
  template: require('./template.html'),
})
export default class View implements OnInit {
  private data: ApiProviderFindOneModel = null;
  private dataId: string = '';
  private formAccountNumber: string = '';
  private formBeneficiaryType: string = '';
  private formClabe: string = '';
  private formFirstName: string = '';
  private formLastName: string = '';
  private formBeneficiaryName: string = '';
  private uiError: boolean = false;
  private uiWaiting: boolean = false;

  public constructor(
    private apiProviderFindOne: ApiProviderFindOneService,
    private apiProviderUpdate: ApiProviderUpdateService,
    private route: ActivatedRoute,
    private router: Router,
  ) {}

  public ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.apiProviderFindOne.exec({
        id: params.id,
      }).subscribe(result => {
        this.data = result;
        this.dataId = result.id;
        this.formAccountNumber = result.accountNumber;
        this.formClabe = result.clabe;
        this.formBeneficiaryName = result.beneficiaryName;
        this.formBeneficiaryType = result.beneficiaryType;
        this.formFirstName = result.firstName;
        this.formLastName = result.lastName;
        this.formBeneficiaryName = result.beneficiaryName;
      }, err => {});
    });
  }

  private submit(): void {
    switch(this.formBeneficiaryType) {
      case 'CORPORATE':
        var name = this.formBeneficiaryName;
        break;
      case 'INDIVIDUAL':
        var name = `${this.formFirstName} ${this.formLastName}`;
        break;
    }

    this.apiProviderUpdate.exec(this.dataId, {
      accountNumber: this.formAccountNumber,
      beneficiaryType: this.formBeneficiaryType,
      clabe: this.formClabe,
      country: 'MX',
      firstName: this.formFirstName,
      lastName: this.formLastName,
      beneficiaryName: this.formBeneficiaryName,
      name: name,
    }).subscribe(result => {
      const ts2 = timeout - (Date.now() - ts);
      setTimeout(() => {
        this.router.navigate([`/app/page/provider/view/${this.data.id}`]);
      }, ts2 > 0 ? ts2 : 0);
    }, err => {
      const ts2 = timeout - (Date.now() - ts);
      setTimeout(() => {
        this.uiError = true;
        this.uiWaiting = false;
      }, ts2 > 0 ? ts2 : 0);
    });

    const timeout = 1000;
    const ts = Date.now();
    this.uiWaiting = true;
  }
}



