import {
  Component,
  OnInit,
  Inject,
} from '@angular/core';
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
} from '@angular/material';

import ApiSwConfirmService from '../../../../_api/sw/confirm/service'
import ApiSwTransferService from '../../../../_api/sw/transfer/service'

@Component({
  selector: 'my-page-wallet-list-transfer-dialog',
  template: require('./transfer-dialog.html')
})
export default class View implements OnInit {
  private quote: any;

  constructor(
    private apiSwConfirm: ApiSwConfirmService,
    private apiSwTransfer: ApiSwTransferService,
    private dialogRef: MatDialogRef<View>,
    @Inject(MAT_DIALOG_DATA) private data: any,
  ) {}

  ngOnInit() {
    const transferInput = {
      currency: this.data.destCurrency,
      amount: this.data.destAmount,
    };
    this.apiSwTransfer.run({
      currency: 'USD',
      amount: this.data.amount,
      wallet: this.data.destination,
      company: this.data.company,
    }).subscribe(result => {
      this.quote = {
        sw: result,
      };
    }, err => {});
  }

  confirm() {
    const input = {
      id: this.quote.sw.id,
    };
    this.quote.confirm = {}
    this.apiSwConfirm.run(input).subscribe(result => {
      this.quote.confirm.sw = result;
    }, err => {});
  }
}



