import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import UserDestroyService from '../../../_api/user/destroy/service';
import UserFindService from '../../../_api/user/find/service';

@Component({
  selector: 'my-page-users-view',
  template: require('./users.view.component.html'),
})
export default class PageUsersViewComponent implements OnInit {
  email: string;
  password: string;
  userId: string;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private userDestroyService: UserDestroyService,
    private userFindService: UserFindService,
  ) {}

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.userId = params.id;
      this.userFindService.exec({
        id: params.id,
      }).subscribe(users => {
        const user = users[0]
        if (user !== undefined) {
          this.email = user.email;
          this.password = user.password;
        }
      }, err => {});
    });
  }

  deleteUser(): void {
    this.userDestroyService.exec({
      id: this.userId,
    }).subscribe(result => {
      this.router.navigate(['/app/page/users']);
    }, err => {});
  }
}



