import {
  Component,
  OnInit,
} from '@angular/core';
import { Router } from '@angular/router';

import ApiProviderFindModel from '../../../_api/provider/find/output';
import ApiProviderFindService from '../../../_api/provider/find/service';

@Component({
  selector: 'my-page-provider-list',
  template: require('./provider.list.component.html'),
})
export default class View implements OnInit {
  private dataProviders: ApiProviderFindModel[] = null;

  public constructor(
    private apiProviderFind: ApiProviderFindService,
    private router: Router,
  ) {}

  public ngOnInit(): void {
    this.apiProviderFind.exec({}).subscribe(result => {
      this.dataProviders = result;
    }, err => {});
  }

  private addBusiness(): void {
    this.router.navigate(['/app/page/provider/add'], {
      queryParams: {
        type: 'CORPORATE',
      },
    });
  }

  private addIndividual(): void {
    this.router.navigate(['/app/page/provider/add'], {
      queryParams: {
        type: 'INDIVIDUAL',
      },
    });
  }
}



